@media only screen and (max-width: 1259px) {
  .reserve__button {
    width: 200px;
  }
}

@media only screen and (max-width: 767px) {
  .reserve__button {
    width: 100%;
    padding: 10px;
  }
}

.reserve__button:first-child {
  margin-right: 24px;
}

@media only screen and (max-width: 767px) {
  .reserve__button:first-child {
    margin-right: 0;
    margin-bottom: 16px;
  }
}

.reserve__button:hover {
  border: 2px solid #8ade6d;
}

.reserve__button_highlighted {
  border: 2px solid #8ade6d;
}

.reserve__button_highlighted:hover {
  color: #fff;
  border-color: #8ade6d;
  background-color: #8ade6d;
}

[data-modal] {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  padding: 20px;
  opacity: 0;
  visibility: hidden;
  text-align: center;
  font-size: 0;
  white-space: nowrap;
  transform: translate3d(0, 0, 0);
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
  background-color: rgba(0, 0, 0, 0.5);
}

[data-modal]:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

[data-modal].is-open {
  opacity: 1;
  visibility: visible;
  z-index: 1000;
}

[data-modal-container] {
  position: relative;
  padding: 45px 35px 48px;
  z-index: 20;
  border-radius: 4px;
  width: 100%;
  max-width: 570px;
  white-space: normal;
  text-align: left;
  vertical-align: middle;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 0 25px rgba(43, 45, 66, 0.25);
  display: inline-block;
}

[data-modal-close] {
  position: absolute;
  top: 0;
  right: 0;
  padding: 20px;
  background-color: transparent;
  font-size: 0;
}

.modal__close .icon {
  color: #ccc;
  font-size: 14px;
}
